import React, { useMemo, useCallback } from 'react';
import styled from 'styled-components';
import { QuoteBetaNormal } from '@ali/we-design';
import { Controller, Block } from '@ali/4ever-cangjie';
import { Code, CodeRole } from '@ali/4ever-plugin-code';
import { ParagraphRole } from '@ali/4ever-plugin-paragraph';
import { HeadingRole } from '@ali/4ever-plugin-heading';
import { ListRole } from '@ali/4ever-plugin-list';
import { CalloutRole } from '@ali/4ever-plugin-callout';
import { BlockquoteRole } from '@ali/4ever-plugin-blockquote';
import { theme } from '@ali/4ever-utils';
import {
  DocToolbarH1Normal,
  DocToolbarH2Normal,
  DocToolbarH3Normal,
  DocToolbarH4Normal,
  ToolbarOrderListNormal,
  ToolbarCheckBoxNormal,
  ToolbarUnorderListNormal,
  DocToolbarTextNormal,
  DocHighBlockNormal,
  ChangetoNormal,
  SelectedNormalNormal,
} from '@ali/we-icon';
import { isSubjectHeading } from '@ali/4ever-plugin-subject';
import {
  TYPE_GROUP,
  TYPE_MENU_ITEM,
  TYPE_SUB_MENU,
  IconSquareBrackets,
} from '@ali/4ever-component';
import isConvertSelected from '../utils/isConvertSelected';

const commonHeadStyle = {
  padding: '6px 8px',
  height: 'auto',
  fontWeight: 500,
};

const Selected = styled.span`
  margin-left: 20px;
  svg {
    fill: #0089FF;
  }
`;

export default function useTransformMenus(
  locale?: Record<string, string>,
  currentBlock?: Block,
  useCustomBlock?: boolean,
) {
  const controller = Controller.useController();
  const disableSubjectHeading = useMemo(() => {
    return isSubjectHeading(currentBlock);
  }, [currentBlock]);
  const disableMenu = disableSubjectHeading || controller.query('isListSymbolSelected');
  const isCode = Code.isCode(currentBlock);
  
  const extras = useMemo(() => {
    const extrasMap: Record<string, JSX.Element> = {};
    [ 
      'paragraph',
      'h1',
      'h2',
      'h3',
      'h4',
      'olist',
      'ulist',
      'tlist',
      'blockquote',
      'code',
      'callout',
    ].forEach((key) => {
      if (!useCustomBlock && isConvertSelected(key, controller)) {
        // 代表没有指定 currentBlock，由当前选区决定（选区工具栏场景）
        extrasMap[key] = <Selected><SelectedNormalNormal /></Selected>;
      } else if (currentBlock && isConvertSelected(key, controller, currentBlock)) {
        extrasMap[key] = <Selected><SelectedNormalNormal /></Selected>;
      }
    });
    return extrasMap;
  }, [currentBlock, controller.value, useCustomBlock]);

  const menus = useMemo(() => {
    return {
      type: TYPE_SUB_MENU,
      icon: <ChangetoNormal />,
      title: locale?.transform,
      maxHeight: 550,
      overlayStyle: {
        minWidth: 173,
        overflow: 'auto',
      },
      menus: [{
        type: TYPE_GROUP,
        title: locale?.block,
        menus: [{
          type: TYPE_MENU_ITEM,
          menuKey: 'heading.paragraph',
          title: locale?.paragraph,
          icon: <DocToolbarTextNormal />,
          action: 'format',
          disabled: disableMenu,
          role: ParagraphRole.paragraph,
          extra: extras['paragraph'],
        },
        {
          type: TYPE_MENU_ITEM,
          menuKey: 'heading.heading1',
          title: locale?.h1,
          icon: <DocToolbarH1Normal />,
          action: 'format',
          disabled: disableMenu,
          role: HeadingRole.heading1,
          extra: extras['h1'],
          style: {
            fontSize: theme['heading-1']['font-size'],
            lineHeight: theme['heading-1']['line-height'],
            ...commonHeadStyle,
          }
        },
        {
          type: TYPE_MENU_ITEM,
          title: locale?.h2,
          menuKey: 'heading.heading2',
          icon: <DocToolbarH2Normal />,
          action: 'format',
          disabled: disableMenu,
          role: HeadingRole.heading2,
          extra: extras['h2'],
          style: {
            fontSize: theme['heading-2']['font-size'],
            lineHeight: theme['heading-2']['line-height'],
            ...commonHeadStyle,
          }
        },
        {
          type: TYPE_MENU_ITEM,
          menuKey: 'heading.heading3',
          title: locale?.h3,
          icon: <DocToolbarH3Normal />,
          action: 'format',
          disabled: disableMenu,
          role: HeadingRole.heading3,
          extra: extras['h3'],
          style: {
            fontSize: theme['heading-3']['font-size'],
            lineHeight: theme['heading-3']['line-height'],
            ...commonHeadStyle,
          }
        },
        {
          type: TYPE_MENU_ITEM,
          menuKey: 'heading.heading4',
          title: locale?.h4,
          icon: <DocToolbarH4Normal />,
          action: 'format',
          disabled: disableMenu,
          role: HeadingRole.heading4,
          extra: extras['h4'],
          style: {
            fontSize: theme['heading-4']['font-size'],
            lineHeight: theme['heading-4']['line-height'],
            ...commonHeadStyle,
          }
        }],
      }, {
        type: TYPE_GROUP,
        title: locale?.list,
        menus: [{
          type: TYPE_MENU_ITEM,
          menuKey: 'list.olist',
          title: locale?.olist,
          icon: <ToolbarOrderListNormal />,
          action: 'format',
          disabled: disableSubjectHeading,
          role: ListRole.listOrdered,
          extra: extras['olist'],
        },
        {
          type: TYPE_MENU_ITEM,
          menuKey: 'list.ulist',
          title: locale?.ulist,
          icon: <ToolbarUnorderListNormal />,
          action: 'format',
          disabled: disableSubjectHeading,
          role: ListRole.listUnordered,
          extra: extras['ulist'],
        },
        {
          type: TYPE_MENU_ITEM,
          menuKey: 'list.tlist',
          title: locale?.tlist,
          icon: <ToolbarCheckBoxNormal />,
          action: 'format',
          disabled: disableSubjectHeading,
          role: ListRole.listTask,
          extra: extras['tlist'],
        }],
      }, {
        type: TYPE_GROUP,
        title: locale?.block,
        menus: [
          {
            type: TYPE_MENU_ITEM,
            title: locale?.blockquote,
            menuKey: 'blockquote',
            icon: <QuoteBetaNormal />,
            action: 'format',
            disabled: disableMenu || isCode,
            role: BlockquoteRole.blockquote,
            extra: extras['blockquote'],
          },
          {
            type: TYPE_MENU_ITEM,
            title: locale?.code,
            menuKey: 'code',
            icon: <IconSquareBrackets />,
            action: 'format',
            disabled: disableMenu,
            role: CodeRole.code,
            // 为 undefined 会导致显示快捷键
            extra: extras['code'] || '',
          },
          {
            type: TYPE_MENU_ITEM,
            title: locale?.callout,
            menuKey: 'callout',
            icon: <DocHighBlockNormal />,
            action: 'format',
            disabled: disableMenu || isCode,
            role: CalloutRole.callout,
            extra: extras['callout'],
          },
        ],
      }],
    };
  }, [extras, locale, disableMenu, isCode, disableSubjectHeading]);

  return {
    extras,
    menus,
  };
}

